
import './App.css';
import React, {useState, useContext, useEffect} from 'react';
import axios from 'axios';
import BannerImage from './components/BannerImage';
import {FormControl, InputLabel, MenuItem, ThemeProvider, Select, Box, Button} from "@mui/material";
import {theme} from './components/theme';
import {DiscreteSliderLabel} from './components/Slider';
import {SimpleSnackbar} from './components/SnackBar';
import { BrowserRouter as Router, Route, Routes, NavLink} from 'react-router-dom';
import NavBar from './components/NavBar';

/* Here's my beautiful app! */
function App() {
  const url = '/api/contacts/getAll';
  const foodUrl = '/api/food/getAll';
  const [partners, setPartners] = useState([]);
  const [foods, setFoods] = useState([]);

  useEffect(() => {
    axios.get(url).then(res => {
      setPartners(res.data);
    })
  }, [])

  useEffect(() => {
    axios.get(foodUrl).then(res => {
      setFoods(res.data);
    })
  }, [])

  return (
    <div className="App">
      <header className="App-header">
        <BannerImage/>
        <p>
          Are you <b>HANGRY?</b> 
        </p>
        <ThemeProvider theme={theme}>
        <DiscreteSliderLabel />
          <FormControl 
              sx={{
                width: 300
              }}>
            <InputLabel color="neutral" id="demo-simple-select-label"> Bae </InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              label="Partner"
              color = "neutral"
            >
              {partners.map ((partner) => 
              <MenuItem value={partner.seqNum}>{partner.name}</MenuItem>   
              )}      
            </Select>
          </FormControl>
        <br></br>

        <FormControl 
              sx={{
                width: 300
              }}>
            <InputLabel color="neutral" id="demo-simple-select-label"> Food </InputLabel>
            <Select
              labelId="demo-simple-select-label"
              id="demo-simple-select"
              label="Food"
              color = "neutral"
            >
              {foods.map ((foods) => 
              <MenuItem value={foods.foodSeqNum}>{foods.name + " (" + foods.calories + " kcal)"}</MenuItem>   
              )}      
            </Select>
          </FormControl>
          <br></br>
        <SimpleSnackbar/>
        </ThemeProvider>
        <br></br>
        <br></br>
      </header>
    </div>
  );
}

export default App;