import '../App.css';
import NavBar from '../components/NavBar';
import axios from "axios";
import React, { useState, useEffect } from "react";
import menu from "../assets/menu.jpeg";
import {TextField, Grid, ThemeProvider} from '@mui/material';
import {theme} from '../components/theme';

export default function Food() {
    const url = '/api/food/getAll';
    const [foods, setFoods] = useState([]);
  
  useEffect(() => {
    axios.get(url).then(res => {
      setFoods(res.data);
    })
  }, [])

  return (
    <div className="App">

       <header className="App-header">
       <img className="banner-menu" src={menu} alt="BannerImage"/>
       <br></br>     <br></br>     <br></br>
              <table>
                  {foods.map ((value) => 
                  <tr> 
                    <td>
                      {value.name}
                    </td>
                    <td>
                      {value.calories + "kcal"}
                    </td>
                    <td>
                      {"" + value.price}
                    </td>
                    <td>
                    <img src={value.picture} height="275px" width="auto"/>
                    </td>
                  </tr>
                  )}
              </table>
        <div>
              
             
        </div>
       </header>
    </div>
  );
}

