import * as React from 'react';
import Button from '@mui/material/Button';
import Snackbar from '@mui/material/Snackbar';
import IconButton from '@mui/material/IconButton';
import CloseIcon from '@mui/icons-material/Close';

export function SimpleSnackbar() {
  const [open, setOpen] = React.useState(false);

  /* Run API call when click */

  function callAPI(){
  fetch ('https://mocki.io/v1/2f1d63db-81ac-475e-aa98-efe41cf8b1df', {method: 'GET'})
    .then (data => data.json())
    .then((response) => console.log(JSON.stringify(response)))
  };

  const handleClick = () => {
    setOpen(true);
  };

  const handleClose = (event, reason) => {
    if (reason === 'clickaway') {
      return;
    }

    setOpen(false);
  };

  const action = (
    <React.Fragment>
      <Button color="neutral" size="small" onClick={handleClose}>
        UNDO
      </Button>
      <IconButton
        size="small"
        aria-label="close"
        color="inherit"
        onClick={handleClose}
      >
        <CloseIcon fontSize="small" />
      </IconButton>
    </React.Fragment>
  );



  return (
    <div>
       <Button 
          sx={{
            width: 450,
            fontSize: 24
          }}
          variant="contained" color = "neutral"
          onClick={ () =>{
            callAPI();
            handleClick();
          }
          }
        > 
          Feed me
        </Button>
      <Snackbar
        open={open}
        autoHideDuration={3000}
        onClose={handleClose}
        message= {"Message Sent!"}
        action={action}
      />
    </div>
  );
}
