import '../App.css';
import NavBar from '../components/NavBar';
import axios from "axios";
import React, { useState, useEffect } from "react";
import partner from "../assets/partner.jpeg";
import {TextField, Grid, ThemeProvider, Button} from '@mui/material';
import SendIcon from '@mui/icons-material/Send';
import {theme} from '../components/theme';


export default function Partners() {
  const url = '/api/contacts/getAll';
  const [partners, setPartners] = useState([]);
  const [inputName, setInputName] = useState();
  const [inputPhoneNumber, setInputPhoneNumber] = useState();

  useEffect(() => {
    axios.get(url).then(res => {
      setPartners(res.data);
    })
  }, [useEffect])

  const handleNameChange = (event) => {
    setInputName(event.target.value);
  }

  const handlePhoneNumberChange = (event) => {
    setInputPhoneNumber(event.target.value);
  }

  function CreateContact(){
    const createContactUrl = '/api/contacts/post';
    const validName = new RegExp(
      '^[a-zA-Z]+$'
    );

    const validPhoneNumber = new RegExp(
      '^[0-9]+$'
    );

    if (!validName.test(inputName)){
      alert("Name can only contain alphabets, sorry Elon.");
    }
    if (!validPhoneNumber.test(inputPhoneNumber)){
      alert("Phone numbers can only contain - yeah you've guessed it - numbers.");
    }
    else
      axios.post(createContactUrl, {
        name: (inputName),
        phoneNumber: (inputPhoneNumber),
        seqNum: 6
      })
      .then(res => {
        alert(JSON.stringify(res.data) + " was created");
      })
    };
  
    return (
      <div className="App">
         <header className="App-header">
         <div>
            <img className="banner-partners" src={partner} alt="BannerImage" />
        </div>
        <div>
        <br></br>
          <Grid container>
            <Grid item xs={5}>
                <table align='right'>
                    <tr>
                      <th>Name</th>
                      <th>Phone Number</th>
                    </tr>
                    {partners.map ((value) => 
                    <tr> 
                      <td>
                        {value.name}
                      </td>
                      <td>
                        {value.phoneNumber.slice(0,3) + "***"}
                      </td>
                    </tr>
                    )}
                </table>
            </Grid>
            <Grid item xs={6}>
              Can't find your partner? Create one!
                <br></br><br></br>
                <ThemeProvider theme={theme}>
                  <TextField 
                    required id="outlined-basic" 
                    label="Name" 
                    variant="outlined" 
                    color="neutral" 
                    onChange={handleNameChange}
                  />
                  <br></br> <br></br>
                  <TextField 
                    required id="outlined-basic" 
                    label="Phone Number" 
                    variant="outlined" 
                    color="neutral"  
                    onChange={handlePhoneNumberChange}
                  />
                  <br></br> <br></br>
                <Button 
                  variant="contained" 
                  color="neutral" 
                  endIcon={<SendIcon />}
                  onClick={ () =>{
                    CreateContact()
                  }
                }
                >
                submit
                </Button>
                </ThemeProvider>
            </Grid>
          </Grid>
          </div>
         </header>
      </div>
    );
  }